body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-linear-gradient(right, rgb(7,27,82) 0%, rgb(0,128,128) 100%);
  background: linear-gradient(to left, rgb(7,27,82) 0%, rgb(0,128,128) 100%);
}

@font-face {
	font-family: 'Space Age';
	font-style: normal;
	font-weight: normal;
	src: local('Space Age'), url("/static/media/space age.b750cbfd.woff") format('woff');
}

#main-title {
	font-family: 'Space Age';
	font-size: 4em;
	margin-top: 0.4em;
	margin-bottom: .4em;
	text-shadow: 5px 5px 20px #1F0439;
	color: #0CCAC4;
}

#search-box {
	box-shadow: inset 3.5px 3.5px 10px #434343;
}
